 {\n    font-size: 12px;\n    display: flex;\n    flex-direction: column;\n}\n\nheader {\n    display: flex;\n    justify-content: space-between;\n    width: 100%;\n    height: fit-content;\n    background-color: #fff;\n    box-shadow: rgba(0, 0, 0, 0.25) 1px 5px 5px;\n    position: fixed;\n    top: 0;\n\n    #title {\n        @include textGradient();\n        @include kaisei-harunoumi($weight: 700);\n        font-size: 2rem;\n        border-bottom: black solid 0.2rem;\n        width: calc(30%);\n        text-align: center;\n        padding: 16px;\n    }\n\n    nav {\n        display: flex;\n        margin-right: 1rem;\n        button {\n            @include kaisei-harunoumi($weight: 700);\n            font-size: 1.5rem;\n            appearance: none;\n            background-color: transparent;\n            border: none;\n            color: #3b3b3b;\n            cursor: pointer;\n            outline: none;\n            padding: 16px 24px;\n            text-align: center;\n            text-decoration: none;\n            user-select: none;\n            -webkit-user-select: none;\n            width: 100%;\n            height: 90%;\n            align-self: center;\n        }\n\n        button:disabled {\n            pointer-events: none;\n        }\n\n        button:hover {\n            color: #fff;\n            background-color: #1a1a1a;\n            box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n            border-radius: 15px;\n        }\n\n        button:active {\n            box-shadow: none;\n        }\n    }\n}\n\n#content {\n    display: flex;\n    margin-top: 5rem;\n    #leftPart {\n        display: flex;\n        flex-direction: column;\n        width: 50%;\n        padding-left: 5%;\n        padding-top: 3%;\n        padding-right: 0;\n        #logo_title {\n            @include kaisei-harunoumi();\n            font-size: 5rem;\n        }\n        #logo {\n            width: 30%;\n        }\n    }\n    #rightPart {\n        width: 50%;\n        display: flex;\n        flex-direction: column;\n        align-content: center;\n        justify-content: center;\n\n        #slogan {\n            @include kaisei-harunoumi(700);\n            @include textGradient();\n            font-size: 3rem;\n            word-wrap: break-word;\n        }\n\n        .location{\n            margin-top: 2rem;\n            margin-bottom: 2rem;\n        }\n\n        img{\n            width: 30%;\n            align-self: center;\n            padding-right: 1rem;\n        }\n\n        h1{\n            @include kaisei-harunoumi();\n            font-size: 2rem;\n        }\n\n        p, li{\n            @include kaisei-harunoumi();\n            font-size: 1.5rem;\n            margin: 1rem;\n        }\n    }\n}\n"],"sourceRoot":""}]);
